.add_song {
  margin: 30px auto;
}
@media screen and (max-width: 768px) {
  .add_song {
    width: 90%;
    margin: 14px 20px;
  }
}

.add_title {
  margin-bottom: 25px;
  font-size: 3.6rem;
  text-align: center;
}
@media screen and (max-width: 768px) {
  .add_title {
    font-size: 1.8rem;
    text-align: left;
  }
}

.add_list li {
  margin-bottom: 10px;
}
@media screen and (max-width: 768px) {
  .add_list li {
    margin-bottom: 14px;
  }
}
.add_list li:nth-last-child(2) {
  margin-bottom: 15px;
}
@media screen and (max-width: 768px) {
  .add_list li:nth-last-child(2) {
    margin-bottom: 20px;
  }
}
.add_list li:last-child {
  margin-bottom: 0;
}
@media screen and (max-width: 768px) {
  .add_list {
    width: 100%;
  }
}

.caption_mb {
  margin-bottom: 7px;
}

.form_lg {
  width: 300px;
  height: 30px;
}
@media screen and (max-width: 768px) {
  .form_lg {
    width: 100%;
    height: 40px;
  }
}

.select_size {
  width: 107px;
  height: 30px;
}
@media screen and (max-width: 768px) {
  .select_size {
    width: 142px;
    height: 40px;
  }
}

textarea {
  width: 300px;
  height: 127px;
}
@media screen and (max-width: 768px) {
  textarea {
    width: 100%;
    height: 170px;
  }
}